import React from 'react';
import './App.css';
import * as microsoftTeams from "@microsoft/teams-js";

interface IState {
  context?: microsoftTeams.Context,
  members?: microsoftTeams.ChatMembersInformation
}

/**
 * The 'PersonalTab' component renders the main tab content
 * of your app.
 */
class Tab extends React.Component<any, IState> {
  constructor(props: any) {
    super(props)
    this.state = {
      context: undefined,
      members: undefined
    }
  }

  //React lifecycle method that gets called once a component has finished mounting
  //Learn more: https://reactjs.org/docs/react-component.html#componentdidmount
  componentDidMount() {
    // Get the user context from Teams and set it in the state
    microsoftTeams.getContext((context: microsoftTeams.Context) => {
      this.setState({
        ...this.state,
        context: context
      });
    });
    microsoftTeams.getChatMembers((members: microsoftTeams.ChatMembersInformation) => {
      this.setState({
        ...this.state,
        members: members
      });
    })
    // Next steps: Error handling using the error object
  }

  render() {

    const userName = this.state.context ? Object.keys(this.state.context).length > 0 ? this.state.context['upn'] : "" : "";

    return (
      <div>
        <h3>Hello World!</h3>
        <h1>Congratulations {userName}!</h1> <h3>This is the tab you made :-)</h3>

        <div>
          {
            this.state.members?.members.map(m =>
              <div>{m.upn}</div>
            )
          }
        </div>
      </div>
    );
  }
}
export default Tab;